<template>
  <div class="login-container" :class="{ 'loading': loading }">
    <div class="login-card">
      <div class="logo">
        <img src="@/assets/icons/logo@2x.png" alt="Logo" />
      </div>
      
      <el-form @submit.prevent="handleLogin">
        <el-form-item>
          <el-input
            v-model="loginForm.username"
            placeholder="用户名"
            :prefix-icon="User"
            size="large"
            @keyup.enter="focusPassword"
            ref="usernameInputRef"
            :disabled="loading"
          />
        </el-form-item>
        
        <el-form-item>
          <el-input
            v-model="loginForm.password"
            :type="togglePasswordVisible ? 'text' : 'password'"
            placeholder="密码"
            :prefix-icon="Lock"
            size="large"
            @keyup.enter="handleLogin"
            ref="passwordInputRef"
            :disabled="loading"
          >
            <template #suffix>
              <el-icon 
                class="password-toggle" 
                @click="togglePasswordVisible = !togglePasswordVisible"
                :class="{ 'disabled': loading }"
              >
                <component :is="togglePasswordVisible ? 'View' : 'Hide'" />
              </el-icon>
            </template>
          </el-input>
        </el-form-item>

        <el-button
          type="primary"
          :loading="loading"
          class="login-button"
          size="large"
          @click="handleLogin"
        >
          {{ loading ? '登录中...' : '立即登录' }}
        </el-button>
      </el-form>
    </div>

    <!-- <div class="contact-trigger" @mouseenter="showQrCode = true" @mouseleave="showQrCode = false">
      <div class="contact-text"  >扫码联系我们</div>
      
    </div> -->
    <div class="qr-code " :class="{ 'show': showQrCode }" >
        <img src="@/assets/icons/qrcode.png" alt="QR Code" />
      </div>
    <div class="version">版本号：{{ version }}</div>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive } from 'vue'
import { useRouter } from 'vue-router'
import { useUserStore } from '@/stores/user'
import type { LoginForm } from '@/types/user'
import { ElMessage } from 'element-plus'
import { User, Lock, View, Hide } from '@element-plus/icons-vue'

// 获取版本号
const version = ref(window.RUNTIME_CONFIG?.VERSION || 'v1.0.0');

const router = useRouter()
const userStore = useUserStore()

const loginForm = reactive<LoginForm>({
  username: '',
  password: ''
})

const loading = ref(false)

const handleLogin = async () => {
  if (!loginForm.username || !loginForm.password) {
    ElMessage.warning('请输入用户名和密码')
    return
  }

  try {
    loading.value = true
    await userStore.login(loginForm.username, loginForm.password)
    ElMessage.success({
      message: '登录成功，欢迎回来！',
      duration: 3000
    })
    // 登录成功后延迟跳转，根据用户角色决定跳转页面
    setTimeout(() => {
      // 如果是管理员账号，跳转到用户管理页面
      if (userStore.userInfo?.username === 'admin') {
        router.push('/user/list')
      } else {
        // 其他账号跳转到项目页面
        router.push('/project')
      }
    }, 500)
  } catch (error: any) {
    ElMessage.error({
      message: error?.message || '登录失败，请检查用户名和密码',
      duration: 3000
    })
  } finally {
    loading.value = false
  }
}

const togglePasswordVisible = ref(false)
const showQrCode = ref(false)

// 添加输入框的引用
const usernameInputRef = ref<HTMLInputElement | null>(null)
const passwordInputRef = ref<HTMLInputElement | null>(null)

// 聚焦到密码输入框
const focusPassword = () => {
  passwordInputRef.value?.focus()
}
</script>

